<template>
  <div v-if="error.truty" style="display: flex; align-items: center; justify-content: center; height: 100vh;">
    <h1 style="text-align: center;">{{error.message}}</h1>
  </div>
  <div v-else>
    <div class="container" :style="{'backgroundImage': `url(${getBgImg()})`}">
      <div v-show="open" style="position: absolute; height:100vh; width: 100vw; background: rgba(0,0,0,0.7);" @click="open = false, openAuto=false"></div>
      <transition name="search-anim">
        <div class="search-box" v-show="open">
          <input @keypress.enter="searchKota(), open = false" v-model="kota" ref="search" type="text" class="search" id="search" placeholder="Search any address . . .  ↵" autocomplete="off" >
          <svg @click="kota = ''" v-show="kota != ''" xmlns="http://www.w3.org/2000/svg" class="close-search" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </div>
      </transition>
      <div class="autocomplete" v-show="openAuto">
        <ul>
          <li v-for="(result,index) in autoCompleteResult" :key="index" @click="kota = result, autoCompleteResult = [],searchKota(), open = false, openAuto = false">{{result}}</li>
        </ul>
      </div>
      <div class="current-info">
        <div class="date-container">
          <div class="time" id="time">{{hour}}:{{minutes}}<span id="am-pm">{{zone}}</span></div>
          <div class="date" id="date">{{day}}, {{date}} {{month}} {{year}}</div>
          <div class="others" id="current-weather-items">
            <div class="weather-items">
              <div class="tempereture">Temperature</div>
              <div>{{Math.round(current.temp)}}&#176;C</div>
            </div>
            <div class="weather-items">
              <div>Humidity</div>
              <div>{{current.humidity}}%</div>
            </div>
            <div class="weather-items">
              <div>Pressure</div>
              <div>{{current.pressure}}hPa</div>
            </div>
            <div class="weather-items">
              <div>Sunrise</div>
              <div>{{current.sunrise}}</div>
            </div>
            <div class="weather-items">
              <div>Sunset</div>
              <div>{{current.sunset}}</div>
            </div>
          </div>
        </div>

        <div class="place-container">
          <svg @click="searchOpen()" xmlns="http://www.w3.org/2000/svg" class="open-search" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          </svg>
          <div class="time-zone" id="time-zone">Time Zone : {{timeZone}}</div>
          <div class="country" id="country">{{address.label}}</div>
        </div>
      </div>
      <div class="future-forecast">
        <div class="today" id="current-temp">
          <img :src="'http://openweathermap.org/img/wn/'+daily[0].weather[0].icon+'@4x.png'" alt="weather icon" class="w-icon" />
          <div class="others">
            <div class="day">{{daily[0].date}}</div>
            <div class="temp">Night <br> {{Math.round(daily[0].temp.night)}}&#176; C</div>
            <div class="temp">Day <br> {{Math.round(daily[0].temp.day)}}&#176; C</div>
          </div>
        </div>
        <div class="weather-forecast" id="weather-forecast">
          <template v-for="(day, index) in daily" :key="index">
            <div v-if="index !== 0" class="weather-forecast-item">
              <div class="day">{{day.date}}</div>
              <img :src="'http://openweathermap.org/img/wn/'+day.weather[0].icon+'@2x.png'" alt="weather icon" class="w-icon" />
              <div class="temp">Night {{Math.round(day.temp.night)}}&#176; C</div>
              <div class="temp">Day {{Math.round(day.temp.day)}}&#176; C</div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import moment from 'moment';
import moment from 'moment-timezone'

export default {
  data(){
    return {
      // searching
      open: false,
      openAuto: false,
      kota: '',
      address: [],
      autoCompleteResult: [],
      // location
      lat: '',
      long: '',
      // error
      error: {truty: false, message: ''},
      // time
      year: '',
      month: '',
      date: '',
      day: '',
      hour: '',
      minutes: '',
      // ampm: '',
      zone: '',
      timeZone: '',
      // list api
      API_KEY: '518405935043a2f92c0b7309ffb458c4',
      HERE_KEY: 'wGyfN0ceQeD0CQW4ZtHBPMawNED5SWBCiNRT849mUKM',
      // result weatherMap
      current: [],
      daily: [],
    }
  },
  mounted(){
    this.getWeatherData()
    setInterval(()=>{
      if(!this.kota){
        this.getWeatherData()
      } else{
        this.searchKota()
      }
    },1000*60)
    setInterval(()=>{
      // const time = new Date()
      let time = moment().tz(this.timeZone)
      this.year = time.format('YYYY')
      this.date = time.format('D')
      this.hour = time.format('HH')
      this.minutes = time.format('mm')
      this.zone = time.format('zz')
      // this.hourIn12HrFormat()
      // this.getAmpm()
      this.getDay()
      this.getMonth()
    },3000)
  },
  updated(){
    // this.openAuto = false
  },
  beforeUnmount(){
    clearInterval()
  },
  methods:{
    // hourIn12HrFormat(){
    //   this.hour >= 13 ? this.hour%12 : this.hour
    // },
    // getAmpm(){
    //   this.ampm = this.hour >= 12 ? 'PM': 'AM'
    // },
    getDay(){
      const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']
      this.day = days[new Date().getDay()]
    },
    getMonth(){
      const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      this.month = months[new Date().getMonth()]
    },
    getWeatherData(){
      if(navigator.geolocation){
        navigator.geolocation.getCurrentPosition(success =>{
          let {latitude, longitude} = success.coords
          this.lat = latitude
          this.long = longitude
          // console.log(this.lat, this.long)
          this.getKota()
          this.getData()
        }, err => {
          this.error.truty = true
          this.error.message = err.message
          if(err.code == 1){
            this.error.message = 'Location service seems off . . .'
          }
        })
      } else{
        this.error.truty = true
        this.error.message = 'Geolocation is not supported in this browser . . .'
      }
    },
    getData(){
      fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.lat}&lon=${this.long}&exclude=hourly,minutely&units=metric&appid=${this.API_KEY}`)
        .then(res => {
          if(res.status == 200){
            return res.json()
          }
          this.error.truty = true
          this.error.message = 'Website temporarily Down . . .'
        })
        .then(data => {
          this.showWeatherData(data)
          // console.log(data)
        })
        .catch(e=>{
          console.log(e)
        })
    },
    showWeatherData(data){
      this.daily = data.daily
      for(let i = 0; i < data.daily.length; i++){
        this.daily[i].date = moment(data.daily[i].dt*1000).format('ddd')
      }
      this.timeZone = data.timezone

      this.current = data.current
      this.current.sunrise = moment(this.current.sunrise*1000).tz(this.timeZone).format('HH:mm z')
      this.current.sunset = moment(this.current.sunset*1000).tz(this.timeZone).format('HH:mm z')
      // console.log(this.current)
    },
    getBgImg(){
      let weather = this.daily[0].weather[0].main.toLowerCase()
      if(weather === 'rain'){
        return require('@/assets/rainbg.webp')
      }
      if(weather === 'clouds'){
        return require('@/assets/cloudbg.webp')
      }
      if(weather === 'clear'){
        return require('@/assets/clearbg.webp')
      }
      if(weather === 'snow'){
        return require('@/assets/snowbg.webp')
      }
    },
    async getKota(){
      await fetch(`https://revgeocode.search.hereapi.com/v1/revgeocode?at=${this.lat},${this.long}&lang=en-US&apiKey=${this.HERE_KEY}`)
        .then(res =>{
          if(res.status == 200){
            return res.json()
          }
        })
        .then(data =>{
          this.address = data.items[0].address
        })
    },
    async searchKota(){
      if (!this.kota){
        return this.getWeatherData()
      }
      let namaKota = this.kota
      await fetch(`https://geocode.search.hereapi.com/v1/geocode?q=${namaKota}&apiKey=${this.HERE_KEY}`)
        .then(res =>{
          if(res.status == 200){
            return res.json()
          }
        })
        .then(data =>{
          // console.log(data)
          this.address = data.items[0].address
          this.lat = data.items[0].position.lat
          this.long = data.items[0].position.lng
          this.getData()
          this.openAuto = false
          this.autoCompleteResult = []
        })
    },
    searchOpen(){
      this.open = !this.open
      this.$nextTick(() => this.$refs.search.focus())
    },
    autoComplete(){
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.timer = setTimeout(() => {
        this.getAutoComplete()
      }, 300);
    },
    async getAutoComplete(){
      // console.log(encodeURI(this.kota))
      if(this.kota){
        await fetch(`https://autocomplete.search.hereapi.com/v1/autocomplete?q=${this.kota}&limit=5&apikey=${this.HERE_KEY}`)
          .then(res => {
            if(res.status == 200){
              return res.json()
            }
          })
          .then(data=>{
            if(this.open == false){
              this.openAuto = false
            }else{
              this.openAuto = true
            }
            // console.log(data.items)
            this.autoCompleteResult = []
            console.log(data)
            for (const resultData of data.items) {
              let label = resultData.address.label
              this.autoCompleteResult.push(label)
            }
          })
      } else{
        this.autoCompleteResult = []
        this.openAuto = false
      }
    }
  },
  watch:{
    kota(){
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.timer = setTimeout(() => {
        this.getAutoComplete()
      }, 300);
    }
  }
};
</script>

